/* CSS Custom Properties - Brand Colors */
:root {
  --dark-blue: #183A60;
  --medium-blue: #0095CF;
  --light-lavender: #D4DBF5;
  --orange: #FF7F00;
  --yellow: #FEC401;
  
  /* Typography */
  --font-primary: 'Inter', -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen', 'Ubuntu', 'Cantarell', sans-serif;
  --font-heading: 'Inter', -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen', 'Ubuntu', 'Cantarell', sans-serif;
  
  /* Spacing */
  --spacing-xs: 0.5rem;
  --spacing-sm: 1rem;
  --spacing-md: 1.5rem;
  --spacing-lg: 2rem;
  --spacing-xl: 3rem;
  --spacing-2xl: 4rem;
  
  /* Border radius */
  --radius-sm: 4px;
  --radius-md: 8px;
  --radius-lg: 12px;
  --radius-xl: 16px;
  
  /* Shadows */
  --shadow-sm: 0 1px 3px rgba(24, 58, 96, 0.1);
  --shadow-md: 0 4px 12px rgba(24, 58, 96, 0.15);
  --shadow-lg: 0 8px 24px rgba(24, 58, 96, 0.2);
}

* {
  box-sizing: border-box;
}

html {
  scroll-behavior: smooth;
  scroll-padding-top: 200px;
}

body {
  font-family: var(--font-primary);
  margin: 0;
  color: var(--dark-blue);
  line-height: 1.6;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  scroll-padding-top: 150px;
}

header {
  background: var(--dark-blue);
  color: white;
  padding: var(--spacing-md) 0;
  box-shadow: var(--shadow-md);
  position: sticky;
  top: 0;
  z-index: 1000;
  overflow: hidden;
}

.header-content {
  max-width: 1200px;
  margin: 0 auto;
  display: flex;
  justify-content: space-between;
  align-items: center;
  padding: 0 var(--spacing-md);
}

.logo h1 {
  font-family: var(--font-heading);
  font-size: 2rem;
  font-weight: 700;
  margin: 0;
  text-shadow: 0 2px 4px rgba(0, 0, 0, 0.3);
  position: relative;
  z-index: 1;
}

.logo .subtitle {
  font-size: 1rem;
  font-weight: 400;
  margin-top: var(--spacing-xs);
  opacity: 0.9;
  position: relative;
  z-index: 1;
}

.main-nav {
  display: flex;
  gap: var(--spacing-lg);
  position: relative;
  z-index: 1;
}

.nav-link {
  color: white;
  text-decoration: none;
  font-weight: 500;
  font-size: 1.1rem;
  padding: var(--spacing-xs) var(--spacing-sm);
  border-radius: var(--radius-sm);
  transition: all 0.3s ease;
  position: relative;
}

.nav-link:hover {
  background: rgba(255, 255, 255, 0.1);
  transform: translateY(-2px);
}

/* Hero Section */
.hero {
  position: relative;
  height: 500px;
  display: flex;
  align-items: center;
  justify-content: center;
  text-align: center;
  overflow: hidden;
}

.hero-image {
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  z-index: 1;
}

.hero-image img {
  width: 100%;
  height: 100%;
  object-fit: cover;
}

.hero-content {
  position: relative;
  z-index: 2;
  max-width: 800px;
  padding: 0 var(--spacing-md);
}

.hero-content h2 {
  font-family: var(--font-heading);
  font-size: 3rem;
  font-weight: 700;
  color: white;
  margin: 0 0 var(--spacing-md) 0;
  line-height: 1.2;
  text-shadow: 2px 2px 4px rgba(0, 0, 0, 0.7);
}

.hero-content p {
  font-size: 1.3rem;
  color: white;
  line-height: 1.6;
  margin: 0 0 var(--spacing-lg) 0;
  text-shadow: 1px 1px 3px rgba(0, 0, 0, 0.7);
}

.hero-cta {
  margin-top: var(--spacing-lg);
}

.cta-button {
  display: inline-block;
  background: var(--orange);
  color: white;
  text-decoration: none;
  padding: var(--spacing-sm) var(--spacing-lg);
  border-radius: var(--radius-lg);
  font-weight: 600;
  font-size: 1.1rem;
  transition: all 0.3s ease;
  box-shadow: var(--shadow-md);
  border: none;
  cursor: pointer;
}

.cta-button:hover {
  background: #ff9500;
  transform: translateY(-2px);
  box-shadow: var(--shadow-lg);
}

/* About Section */
.about {
  background: white;
  padding: var(--spacing-2xl) var(--spacing-md);
  max-width: 1200px;
  margin: 0 auto;
}

.about-content {
  text-align: center;
}

.about-content h2 {
  font-family: var(--font-heading);
  font-size: 2.5rem;
  font-weight: 700;
  color: var(--dark-blue);
  margin: 0 0 var(--spacing-lg) 0;
  line-height: 1.2;
}

.about-description {
  font-size: 1.2rem;
  color: #555;
  line-height: 1.7;
  margin: 0 0 var(--spacing-2xl) 0;
  max-width: 800px;
  margin-left: auto;
  margin-right: auto;
}

.about-features {
  display: grid;
  grid-template-columns: repeat(3, 1fr);
  gap: var(--spacing-xl);
  margin-top: var(--spacing-2xl);
}

.feature {
  text-align: center;
  padding: var(--spacing-lg);
  border-radius: var(--radius-lg);
  background: linear-gradient(135deg, var(--light-lavender) 0%, #f8f9ff 100%);
  transition: transform 0.3s ease;
}

.feature:hover {
  transform: translateY(-4px);
}

.feature h3 {
  font-family: var(--font-heading);
  font-size: 1.5rem;
  font-weight: 600;
  color: var(--dark-blue);
  margin: 0 0 var(--spacing-sm) 0;
}

.feature p {
  font-size: 1rem;
  color: #666;
  line-height: 1.6;
  margin: 0;
}

/* Main Content */
main.post-list {
  display: grid;
  grid-template-columns: repeat(2, 1fr);
  grid-template-rows: repeat(3, auto);
  gap: var(--spacing-lg);
  padding: var(--spacing-xl) var(--spacing-md);
  flex: 1;
  max-width: 1200px;
  margin: 0 auto;
  width: 100%;
}

/* Post Card Styles */
.post-card {
  background: white;
  border-radius: var(--radius-lg);
  box-shadow: var(--shadow-md);
  padding: var(--spacing-lg);
  text-align: left;
  transition: all 0.3s ease;
  border: 1px solid rgba(24, 58, 96, 0.1);
  position: relative;
  overflow: hidden;
  display: flex;
  flex-direction: column;
}

.post-card:hover {
  transform: translateY(-4px);
  box-shadow: var(--shadow-lg);
}

.post-card h2 {
  font-family: var(--font-heading);
  font-size: 1.5rem;
  font-weight: 700;
  color: var(--dark-blue);
  margin: 0 0 var(--spacing-sm) 0;
  line-height: 1.3;
}

.post-card p {
  color: #333;
  margin: 0 0 var(--spacing-md) 0;
  font-size: 0.95rem;
  line-height: 1.5;
  flex-grow: 1;
}

.post-card img {
  width: 100%;
  height: 150px;
  object-fit: cover;
  border-radius: var(--radius-md);
  margin: var(--spacing-sm) 0;
}

.post-card a {
  text-decoration: none;
  color: var(--orange);
  font-weight: 700;
  font-size: 0.9rem;
  display: inline-flex;
  align-items: center;
  gap: var(--spacing-xs);
  transition: color 0.3s ease;
  margin-top: auto;
}

.post-card a:hover {
  color: #ff9500;
}

.post-card a::after {
  content: 'â‰«';
  transition: transform 0.3s ease;
}

.post-card a:hover::after {
  transform: translateX(4px);
}

.post-card .author-date {
  margin-top: var(--spacing-sm);
  font-size: 0.85rem;
  color: #888;
  font-weight: 500;
}

/* Post Page Styles */
.post-page {
  width: 100%;
  max-width: 900px;
  margin: var(--spacing-xl) auto;
  background: white;
  padding: var(--spacing-2xl);
  border-radius: var(--radius-lg);
  box-shadow: var(--shadow-md);
  border: 1px solid rgba(24, 58, 96, 0.1);
  position: relative;
  overflow: hidden;
}

.post-page h1 {
  font-family: var(--font-heading);
  font-size: 2.5rem;
  font-weight: 700;
  color: var(--dark-blue);
  margin: 0 0 var(--spacing-md) 0;
  line-height: 1.2;
}

.post-page h2 {
  font-family: var(--font-heading);
  font-size: 1.75rem;
  font-weight: 600;
  color: var(--dark-blue);
  margin: var(--spacing-lg) 0 var(--spacing-sm) 0;
}

.post-page p {
  color: #555;
  margin: 0 0 var(--spacing-md) 0;
  font-size: 1.1rem;
  line-height: 1.7;
}

.post-page img {
  width: 100%;
  height: auto;
  border-radius: var(--radius-md);
  margin: var(--spacing-md) 0;
  box-shadow: var(--shadow-sm);
}

.post-page a {
  text-decoration: none;
  color: var(--medium-blue);
  font-weight: 600;
  transition: color 0.3s ease;
}

.post-page a:hover {
  color: var(--orange);
}

/* Team Section */
.team {
  background: white;
  padding: var(--spacing-2xl) var(--spacing-sm);
  max-width: 1200px;
  margin: 0 auto;
}

.team-content {
  text-align: center;
}

.team-content h2 {
  font-family: var(--font-heading);
  font-size: 2.5rem;
  font-weight: 700;
  color: var(--dark-blue);
  margin: 0 0 var(--spacing-md) 0;
  line-height: 1.2;
}

.team-description {
  font-size: 1.2rem;
  color: #555;
  line-height: 1.7;
  margin: 0 0 var(--spacing-2xl) 0;
  max-width: 600px;
  margin-left: auto;
  margin-right: auto;
}

.team-grid {
  display: grid;
  grid-template-columns: repeat(4, 1fr);
  gap: var(--spacing-xl);
  margin-top: var(--spacing-2xl);
  max-width: 1200px;
  margin-left: auto;
  margin-right: auto;
}

.team-member {
  text-align: left;
  padding: 0;
  background: none;
  display: flex;
  flex-direction: column;
  align-items: flex-start;
  width: 100%;
  height: 100%;
  justify-content: flex-start;
  min-height: 280px;
}

.team-member:hover {
  transform: none;
}

.member-photo {
  width: 150px;
  height: 150px;
  border-radius: 50%;
  overflow: hidden;
  margin-bottom: var(--spacing-lg);
  border: none;
  box-shadow: none;
  background: #f5f5f5;
}

.member-photo img {
  width: 100%;
  height: 100%;
  object-fit: cover;
}

.team-member h3 {
  font-family: var(--font-heading);
  font-size: 1.1rem;
  font-weight: 700;
  color: #000;
  margin: 0;
  line-height: 1.2;
  text-transform: uppercase;
  letter-spacing: 0.5px;
  min-height: 2.4rem;
  display: flex;
  align-items: flex-start;
  padding: 0;
}

.team-member {
  font-size: 0.9rem;
  color: #000;
  font-weight: 400;
  margin: 0 0 var(--spacing-xs) 0;
  line-height: 1.4;
  min-height: 2.8rem;
  display: flex;
  align-items: flex-start;
}

.role {
  font-size: 0.9rem;
  color: #555;
  font-weight: 400;
  margin: 0 0 var(--spacing-md) 0;
  line-height: 1.4;
  min-height: 2.8rem;
  display: flex;
  align-items: flex-start;
}

.team-member {
  font-size: 0.9rem;
  color: #000;
  font-weight: 400;
  margin: 0 0 var(--spacing-lg) 0;
  line-height: 1.5;
  flex-grow: 1;
  display: flex;
  align-items: flex-start;
  min-height: 4.5rem;
}

.personal-statement {
  margin-top: 0;
}

.social-links {
  display: flex;
  gap: var(--spacing-sm);
  align-items: center;
  margin-top: 0;
}

.social-link {
  width: 24px;
  height: 24px;
  display: flex;
  align-items: center;
  justify-content: center;
  transition: transform 0.2s ease;
}

.social-link:hover {
  transform: scale(1.1);
}

.social-link svg {
  width: 100%;
  height: 100%;
  fill: var(--orange);
}

/* Footer Styles */
footer {
  background: var(--dark-blue);
  color: white;
  padding: var(--spacing-xl) var(--spacing-md);
  text-align: center;
  margin-top: auto;
  position: relative;
  overflow: hidden;
}

footer .copyright {
  margin-top: var(--spacing-lg);
  font-size: 0.9rem;
  opacity: 0.8;
  position: relative;
  z-index: 1;
}
